html,
body {
  text-align: center;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  background-color: #f2f2f2;
  font-family: "Ysabeau", sans-serif;
  color: #000;
}

textarea:focus,
input:focus {
  outline: none;
}

